import { a as apiNames } from "../../../../chunks/apiNames.js";
import { f as fail } from "../../../../chunks/index.js";
import "devalue";
import { s as superValidate, m as message } from "../../../../chunks/superValidate.js";
import "just-clone";
import "ts-deepmerge";
import "memoize-weak";
import { z as zod } from "../../../../chunks/zod.js";
import { z } from "zod";
import { g as getFullName } from "../../../../chunks/member.js";
import nodemailer from "nodemailer";
import { d as private_env } from "../../../../chunks/shared-server.js";
import { a as authorize } from "../../../../chunks/authorization.js";
const transporter = nodemailer.createTransport({
  host: "mailmaster.blossom.dsek.se",
  secure: true,
  port: 465,
  auth: {
    user: private_env.EMAIL_YRKA_USER,
    pass: private_env.EMAIL_YRKA_PASS
  }
});
const load = async ({ locals }) => {
  const { user } = locals;
  authorize(apiNames.YRKA.SEND, user);
  return {
    form: await superValidate(zod(createSchema))
  };
};
const createSchema = z.object({
  title: z.string().min(1).default(""),
  content: z.string().min(1)
});
const actions = {
  default: async ({ request, locals }) => {
    const { member } = locals;
    const form = await superValidate(request, zod(createSchema));
    if (!form) return fail(400, { form });
    const name = member ? getFullName(member) : "Anonym";
    const { title, content } = form.data;
    try {
      const result = await transporter.sendMail({
        from: `"${name}" <yrka@dsek.se>`,
        to: "yrka@dsek.se",
        subject: `YRKANDE: ${title}`,
        text: `${content}`,
        html: `<p>${content}</p><p>Fr√•n: ${name}</p>`
      });
      console.log(result);
    } catch (e) {
      console.error(e);
      return message(
        form,
        {
          message: "Kunde inte skicka e-postmeddelande",
          type: "error"
        },
        { status: 500 }
      );
    }
    return message(form, {
      message: "Yrkande skickat!",
      type: "success"
    });
  }
};
export {
  actions,
  load
};
